
.rickmorty {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 1rem;
    gap: 1rem;
    max-height: 40vh;
    overflow: auto;
}

.character {
    min-height: 200px;
    text-align: center;
    padding: 1rem;
    background-color: #eee;
}

.character img {
    width: 60%;
    border-radius: 50%;
}